<template>
  <component v-if="isDivider" :is="components[type]" v-bind="props">
    <div slot="body" ref="body" class="xdh-form-item__body" v-if="$slots.body">
      <slot name="body"></slot>
    </div>
  </component>
  <el-form-item v-else-if="isShowField"
                class="xdh-form-item"
                :class="itemClasses"
                v-bind="$attrs"
                :rules="checkRules">
    <component :is="components[type]"
               :options="optionsList"
               :prop="$attrs.prop"
               v-bind="props"
               v-on="$listeners"
               v-model="xdhForm.currentModel[$attrs.prop]">
      <slot name="inner"></slot>
    </component>
    <slot></slot>

    <div ref="body" class="xdh-form-item__body" v-if="$slots.body">
      <slot name="body"></slot>
    </div>

  </el-form-item>
</template>

<script>
  /**
   * 
   * @module widgets/xdh-form-item
   *
   */
  import divider from './items/divider'
  import input from './items/input'
  import select from './items/select'
  import radio from './items/radio'
  import checkbox from './items/checkbox'
  import cascader from './items/cascader'
  import date from './items/date'
  import Switch from './items/switch'
  import number from './items/number'
  import rate from './items/rate'
  import slider from './items/slider'
  import time from './items/time'
  import upload from './items/upload'
  import range from './items/range'
  import tag from './items/tag'
  import color from './items/color'
  import editor from './items/editor'
  import inputTag from './items/input-tag'
  import tree from './items/tree'

  function normalOptions(options = []) {
    return options.map(o => {
      return typeof o === 'object' ? o : {
        label: o,
        value: o
      }
    })
  }

  const components = {
    switch: Switch,
    input,
    select,
    radio,
    checkbox,
    cascader,
    date,
    time,
    divider,
    number,
    rate,
    slider,
    upload,
    range,
    tag,
    color,
    editor,
    inputTag,
    tree
  }
  /**
   * 
   * @member slots
   * @property {string} - 
   * @property {string} inner 
   */

  export default {
    name: 'XdhFormItem',
    inject: ['xdhForm'],
    /**
     * , el-form-item
     * @member props
     * @property {String} [type=text]  input number radio checkbox select cascader switch date range color divider
     * @property {String} [dict] xdh-form load  dictMap 
     * @property {Array} [options]  {id, parentId, label, value}, idparentId
     * @property {Object} [props] type
     * @property {Number|String} [contentWidth] 
     * @property {String} [depend] 
     * @property {*} [dependValue] 
     * @property {Boolean} [block=false] 
     */
    props: {
      // ...ElFromItem.props,
      // 
      // input 
      // radio 
      // checkbox 
      // select 
      // cascader 
      // switch 
      // date 
      // range 
      // rate 
      // color 
      // divider 
      // todo  tags array tree color editor xdh-form-array xdh-form-object xdh-form-group
      type: {
        type: String,
        default: 'input',
        validator(val) {
          return !!components[val]
        }
      },
      // 
      dict: String,

      // 
      options: {
        type: Array,
        default() {
          return []
        }
      },
      // 
      props: {
        type: Object,
        default() {
          return {}
        }
      },
      // 
      contentWidth: {
        type: [Number, String]
      },
      // 
      depend: {
        type: String
      },
      // 
      dependValue: {
        type: [String, Number, Boolean, Array]
      },
      // block
      block: {
        type: Boolean,
        default: false
      }
    },
    data() {
      return {
        optionsList: normalOptions(this.options)
      }
    },
    computed: {
      itemClasses() {
        return [
          this.xdhForm.validateMsg ? `xdh-validate--${this.xdhForm.validateMsg}` : '',
          {
            'is-custom-width': !!this.contentWidth,
            'is-depend': !!this.depend,
            'is-block': this.block
          }
        ]
      },
      isDivider() {
        return this.type === 'divider'
      },
      isShowField() {
        // 
        if (!this.depend || this.depend === this.prop || this.xdhForm.designMode) return true
        const dependValue = this.xdhForm.currentModel[this.depend]
        return dependValue === this.dependValue
      },
      checkRules() {
        if (!this.$attrs.rules) return null
        const rules = Array.isArray(this.$attrs.rules) ? this.$attrs.rules : [].concat(this.$attrs.rules)
        return rules.map(r => {
          if (r.pattern && typeof r.pattern === 'string') {
            r.pattern = new RegExp(r.pattern)
          }
          return r
        })
      }
    },
    watch: {
      'xdhForm.$attrs.inline'() {
        this.delaySetContentWidth(this.contentEl)
      },
      'xdhForm.inlineSize'() {
        this.delaySetContentWidth(this.contentEl)
      },
      'xdhForm.$attrs.labelWidth'() {
        this.delaySetContentWidth(this.contentEl)
      },
      '$attrs.labelWidth'() {
        this.delaySetContentWidth(this.contentEl)
      },
      contentWidth() {
        this.delaySetContentWidth(this.contentEl)
      },
      block() {
        this.delaySetContentWidth(this.contentEl)
      },
      labelWidth() {
        this.delaySetContentWidth(this.contentEl)
      },
      options(val) {
        // 
        if (this.dict) return
        this.optionsList = normalOptions(val)
      },
      dict() {
        this.loadOptions()
      },
      isShowField(val) {
        this.$nextTick(() => {
          val && this.init()
        })
      }
    },
    methods: {
      init() {
        if (!this.isDivider && this.$el && this.$el.querySelector) {
          this.contentEl = this.$el.querySelector('.el-form-item__content')
          this.setContentWidth(this.contentEl)
        }
      },
      loadOptions() {
        // 
        if (!this.dict) return

        // 
        let list = this.xdhForm.dictMap[this.dict]
        if (list) {
          this.optionsList = list
          return
        }

        // 
        if (this.xdhForm.load) {
          this.xdhForm.load(this).then(list => {
            this.optionsList = list
          })
        }
      },
      getLabelWidth() {
        return Math.max(
          Number.parseInt(this.$attrs.labelWidth || 0),
          Number.parseInt(this.xdhForm.$attrs.labelWidth || 0),
          0
        )
      },
      setContentWidth(el) {
        if (!el) return

        // 
        if (this.block) {
          const labelWidth = this.getLabelWidth()
          el.style.width = `calc(100% - ${labelWidth}px)`
          return;
        }
        // inline
        if (!this.xdhForm.$attrs.inline) return

        // inlineSize
        if (!this.xdhForm.inlineSize) {
          el.style.width = `auto`
          return
        }
        //  inlineSize
        const map = {
          large: 600,
          medium: 500,
          small: 300
        }
        const labelWidth = this.getLabelWidth()
        const width = Number.parseInt(this.contentWidth) || (map[this.xdhForm.inlineSize] - labelWidth)
        el.style.width = `${width}px`
      },
      delaySetContentWidth(el) {
        this.$nextTick(() => {
          this.setContentWidth(el)
        })
      }
    },
    mounted() {
      this.init()
      if (this.$refs.body) {
        this.$el.appendChild(this.$refs.body)
      }

    },
    created() {
      this.components = components
      this.loadOptions()
    },
    beforeDestroy() {
      this.contentEl = null
      if (this.$refs.body) {
        this.$refs.body.parentNode.removeChild(this.$refs.body)
      }
    }
  }
</script>
